<template>
  <div class="login primaryLight">
    <card
      icon="lock"
      title="Acesso à área administrativa"
      class="form"
    >
      <h1 class="mb-6">Login</h1>
      <input-field
        id="name"
        label="Nome:"
        :outlined="true"
         @onKeyUp="setData"
      />
      <input-field
        id="password"
        label="Senha:"
        :outlined="true"
        type="password"
        @onKeyUp="setData"
      />
      <btn-secondary
        text="Logar"
        :disabled="!enableButton"
        @clicked="login"
      />
    </card>
  </div>
</template>

<script>
import Card from '@/components/globais/Card'
import InputField from '@/components/globais/form/input-field/InputField'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import FormMixin from '@/mixins/FormMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'

export default {
  name: 'Login',
  components: { BtnSecondary, Card, InputField },
  mixins: [ FormMixin, NavigationMixin, SnackbarMixin ],
  data: () => ({
    name: null,
    password: null
  }),
  computed: {
    enableButton () {
      return this.password && this.name
    }
  },
  methods: {
    login () {
      this.$axios.post('/moderator/auth/login', {
        name: this.name,
        password: this.password
      }).then(response => {
        if (response.status === 200) {
          if (Number(response.data.codigo) === 200) {
            localStorage.moderatorToken = response.data.corpo
            setTimeout(() => {
              this.goTo('moderators-barbershops')
            }, 500)
          } else this.setError(response.data.corpo)
        } else this.setError()
      }).catch(() => this.setError())
    },
    setError (msg = 'Não foi possível logar') {
      this.showSnackbarErro(msg)
    }
  }
}
</script>

<style scoped>
.login { display: flex; justify-content: center; align-items: center; min-height: 100vh; }
h1 { text-align: center; margin: 0; font-size: 1.8rem; }
.form { width: 360px; height: 380px; }


</style>